@import '../../../../../assets/theme/colors';
@import '../../../../../assets/theme/fonts.scss';
@import '~bootstrap/scss/functions';

.item-bag-container {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 16px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  overflow: hidden;
  font-size: 12px;
  color: theme-color('slate-gray');
}
.total-bags-collected-container {
  display: flex;
  font-family: 'Inter Medium';
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 16px;
  color: theme-color('primary-blue');
  font-size: 12px;
  border-top: 3px solid rgba(221, 216, 216, 0.1);
}
.item-bag-body {
  padding-top: 2px;
  background-color: white;
  padding-bottom: 2px;
  color: theme-color('slate-gray');
  border-radius: 8px 8px 8px 8px;
  border: 1px solid rgba(79, 77, 77, 0.1);
}

.item-bag-text {
  letter-spacing: 0.1px;
  padding-left: 12px;
  flex: 1;
  font-size: 13px;
}
